//
// Custom Font
//


@font-face {
    font-family: 'Inter';
    font-style:  normal;
    font-weight: 300;
    font-display: swap;
    src: url("../fonts/Inter-Light.ttf") format("woff2"),
         url("../fonts/Inter-Light.ttf") format("woff");
}

@font-face {
    font-family: 'Inter';
    font-style:  normal;
    font-weight: 400;
    font-display: swap;
    src: url("../fonts/Inter-Regular.ttf") format("woff2"),
         url("../fonts/Inter-Regular.ttf") format("woff");
}

@font-face {
    font-family: 'Inter';
    font-style:  normal;
    font-weight: 500;
    font-display: swap;
    src: url("../fonts/Inter-Medium.ttf") format("woff2"),
         url("../fonts/Inter-Medium.ttf") format("woff");
}

@font-face {
    font-family: 'Inter';
    font-style:  normal;
    font-weight: 700;
    font-display: swap;
    src: url("../fonts/Inter-Bold.ttf") format("woff2"),
         url("../fonts/Inter-Bold.ttf") format("woff");
}
