  list-style-type: none;
  color: #303e67;
  line-height: normal;
  text-transform: uppercase;
  flex: 1;
  font-size: 12px;
  position: relative;
  padding: 0 2%;
  font-weight: 500;
  font-family: "Poppins", sans-serif;
}
ul.steppedprogress li:before {
  content: counter(step);
  counter-increment: step;
  width: 24px;
  height: 24px;
  line-height: 20px;
  display: block;
  font-size: 12px;
  color: #303e67;
  background: #f1f5fa;
  border-radius: 50%;
  margin: 0 auto 10px;
  position: relative;
  z-index: 1;
  border: 2px solid #ffffff;
  box-shadow: 0px 0px 0px 1.25px #f1f5fa;
}
ul.steppedprogress li:after {
  content: "";
  width: 100%;
  border: 0.5px dashed #e3ebf6;
  position: absolute;
  left: -50%;
  top: 12px;
  z-index: -1;
}
ul.steppedprogress li.complete:before {
  background: #9ba7ca;
  color: #ffffff;
  border: 2px solid #ffffff;
  box-shadow: 0px 0px 0px 1.25px #9ba7ca;
}
ul.steppedprogress li.complete.continuous:before {
  background: #1761fd;
  color: #ffffff;
  border: 2px solid #ffffff;
  box-shadow: 0px 0px 0px 1.25px #1761fd;
}
ul.steppedprogress li.complete.continuous span {
  color: #1761fd;
}
ul.steppedprogress li.complete.finish:before {
  background: #f5325c;
  color: #ffffff;
  border: 2px solid #ffffff;
  box-shadow: 0px 0px 0px 1.25px #f5325c;
  content: "";
  font: normal normal normal 24px/1 "Material Design Icons";
  font-size: 16px;
  line-height: 20px;
}
ul.steppedprogress li.complete.finish span {
  color: #f5325c;
}
ul.steppedprogress li.complete:after {
  border-color: #d4e0f1;
}
ul.steppedprogress li:first-child:after {
  content: none;
}

@media (max-width: 480px) {
