  margin-right: 0;
  text-transform: uppercase;
}
.blog-card .meta-box li a {
  color: #9ba7ca;
}
.blog-card .meta-box li::after {
  content: "|";
  margin: 0 8px;
}
.blog-card .meta-box li:last-child:after {
  content: "";
}
.blog-card h4 a, .blog-card .h4 a {
  color: #2c3652;
  font-weight: 500;
  font-size: 20px;
  font-family: "Poppins", sans-serif;
}
.blog-card p {
  font-family: "Roboto", sans-serif;
}

.faq-qa li h6, .faq-qa li .h6 {
  font-size: 14px;
  color: #303e67;
  font-weight: 500;
}

#accordionExample-faq .accordion-header {
  line-height: inherit;
}
#accordionExample-faq .accordion-header button {
  padding-left: 48px;
}
#accordionExample-faq .accordion-header button:before {
  content: "";
  font-family: "Font Awesome 5 Free" !important;
  font-weight: 600;
  position: absolute;
  top: 10px;
  left: 10px;
  transition: -webkit-transform 0.25s;
  transition: transform 0.25s;
  transition: transform 0.25s, -webkit-transform 0.25s;
  transform: rotate(180deg);
  color: #1761fd;
  font-size: 10px;
  background-color: rgba(23, 97, 253, 0.15);
  width: 24px;
  height: 24px;
  line-height: 24px;
  border-radius: 50%;
  text-align: center;
}
#accordionExample-faq .accordion-header button.collapsed::before {
  content: "";
  transform: rotate(0deg);
  font-size: 10px;
}

@media print {
  .logo, .page-title, .breadcrumb, .footer {
    display: none;
    margin: 0;
    padding: 0;
  }

  .page-title-box,
.left {
    display: none;
  }

  .content, .page-content-wrapper, .page-wrapper {
    margin-top: 0;
    padding-top: 0;
  }

  .content-page {
    margin-left: 0;
    margin-top: 0;
  }

  .topbar, .footer, .left-sidenav, .report-btn, .leftbar-tab-menu {
    display: none;
    margin: 0;
    padding: 0;
  }

  .content-page > .content {
    margin: 0;
  }

  .page-wrapper .page-content {
    width: 100%;
    margin-top: 0;
  }
}